<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" size="mini" @click="delAll">批量删除</el-button>
        <el-input v-model="select_word" size="mini" placeholder="输入关键字查询歌单" class="handle-input"></el-input>
        <el-button type="primary" size="mini" @click="centerDialogVisible = true">添加歌单</el-button>
      </div>
    </div>
    <el-table size="mini" border style="width:100%" height="680px" :data="data" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="40" align="center"></el-table-column>
        <el-table-column label="歌单图片" width="110" align="center">
          <template slot-scope="scope">
              <div class="songList-img">
                <img :src="getUrl(scope.row.pic)" style="width:100%"/>
              </div>
              <el-upload :action="uploadPic(scope.row.id)" :before-upload="beforeAvatorUpload" :on-success="handleAvatorSuccess">
                <el-button size="mini">更新图片</el-button>
              </el-upload>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="标题" width="120" align="center"></el-table-column>
        <el-table-column label="简介" align="center">
          <template slot-scope="scope">
            <p style="height:100px;overflow:scroll">{{scope.row.introduction}}</p>
          </template>
        </el-table-column>
        <el-table-column prop="style" label="风格" width="120" align="center"></el-table-column>
        <el-table-column label="歌曲管理" width="110" align="center">
          <template slot-scope="scope">
            <p style="height:100px;overflow:scroll">
              <el-button size="mini" @click="songEdit(scope.row.id)">歌曲管理</el-button>
            </p>
          </template>
        </el-table-column>
          <el-table-column label="评论" width="80" align="center">
          <template slot-scope="scope">
           <el-button size="mini" @click="getComment(scope.row.id)">评论管理</el-button>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150" align="center">
           <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination background layout="total,prev,pager,next" :current-page="currentPage" :page-size="pageSize" :total="tableData.length" 
       @current-change="handleCurrentChange">
      </el-pagination>
    </div>
    <el-dialog
      title="添加歌单"
      :visible.sync="centerDialogVisible"
      width="400px"
      center
    >
      <el-form :model="registerForm" ref="registerForm" label-width="80px" :rules="rules">
        <el-form-item prop="title" label="标题" size="mini">
          <el-input v-model="registerForm.title" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item prop="introduction" label="简介" size="mini">
          <el-input v-model="registerForm.introduction" placeholder="简介" type="textarea"></el-input>
        </el-form-item>
        <el-form-item prop="style" label="风格" size="mini">
          <el-input v-model="registerForm.style" placeholder="风格"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
          <el-button size="mini" @click="centerDialogVisible = false">取消</el-button>
          <el-button size="mini" @click="addSongList">确定</el-button>
      </span>
    </el-dialog>
    
    <el-dialog
      title="修改歌单"
      :visible.sync="editVible"
      width="400px"
      center
    >
      <el-form :model="form" ref="form" label-width="80px" :rules="rules">
        <el-form-item prop="title" label="标题" size="mini">
          <el-input v-model="form.title" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item prop="introduction" label="简介" size="mini">
          <el-input v-model="form.introduction" placeholder="简介" type="textarea"></el-input>
        </el-form-item>
           <el-form-item prop="style" label="风格" size="mini">
          <el-input v-model="form.style" placeholder="风格"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
          <el-button size="mini" @click="editVible = false">取消</el-button>
          <el-button size="mini" @click="editSave">确定</el-button>
      </span>
    </el-dialog> 
    
    <el-dialog
      title="删除歌单"
      :visible.sync="deleteVisible"
      width="300px"
      center >
     <div align="center">删除不可恢复,是否确定删除?</div>
      <span slot="footer">
          <el-button size="mini" @click="deleteVisible = false">取消</el-button>
          <el-button size="mini" @click="deleteRow">确定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { setSongList,getAllSongList,updateSongList, deleteSongList} from '../api/index';
import {mixin} from  "../mixins/index";
export default {
  mixins:[mixin],
  data() {
    return {
      centerDialogVisible: false,   //弹窗是否显示
      editVible:false,   //编辑弹窗是否显示
      deleteVisible:false,  //删除弹窗是否显示,
      registerForm: {   //添加框
        title: "",
        introduction: "",
        style: "",
      }, 
      form: {   //编辑框
        id:"",
        title: "",
        introduction: "",
        style: "",
      },
      tableData:[],
      tempData:[],
      select_word:'',
      pageSize:5,
      currentPage:1,
      idx:-1,
      multipleSelection:[], //那些项已经打勾
      rules:{     //添加表单验证规则
          title:[
              {required: true,message:'请输入歌单标题',trigger:'blur'}
          ],
          introduction:[
              {required: true,message:'请输入歌单简介',trigger:'blur'}
          ],
          style:[
              {required: true,message:'请输入歌单风格',trigger:'blur'}
          ],
      }
    }
  },
  computed:{
    //
    data(){
      return this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize);
    }
  },
  watch:{
    select_word:function(){
      if(this.select_word==''){
        this.tableData=this.tempData;
      }else{
        this.tableData=[];
        for(let item of this.tempData){
          if(item.title.includes(this.select_word)){
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created(){
    this.getData();
  },
  methods:{
    //获取当前页
    handleCurrentChange(val){
      this.currentPage=val;
    },
    getData(){
        this.tableData=[];
        this.tempData=[];
        getAllSongList().then(res=>{
          this.tableData = res;
          this.tempData = res;
          this.currentPage=1;
        })
    },

    addSongList(){
      this.$refs['registerForm'].validate(valid=>{  //验证表单数据，是否满足rules
        if(valid){
            let params = new URLSearchParams();
            params.append('title',this.registerForm.title);
            params.append('pic','/img/songListPic/123.jpg');
            params.append('introduction',this.registerForm.introduction);
            params.append('style',this.registerForm.style);
            setSongList(params)
            .then(res=>{
                if(res.code==1){
                  this.getData();
                  this.notify(res.msg,"success");
                }else{
                  this.notify(res.msg,"error");
                }
            })
            .catch(err =>{
                console.log(err);
            });
            this.centerDialogVisible = false;
          }
      });
    },
    //弹出编辑页面
     handleEdit(row){
        this.editVible=true;
        this.form={
        id:row.id,
        title: row.title,
        introduction: row.introduction,
        style: row.style,
        }
    },
    //保存编辑页面修改的数据
    editSave(){
       this.$refs['form'].validate(valid=>{  //验证表单数据，是否满足rules
        if(valid){
            let params = new URLSearchParams();
            params.append('id',this.form.id);
            params.append('title',this.form.title);
            params.append('introduction',this.form.introduction);
            params.append('style',this.form.style);

            updateSongList(params)
            .then(res=>{
                if(res.code==1){
                  this.getData();
                  this.notify(res.msg,"success");
                }else{
                  this.notify(res.msg,"error");
                }
            })
            .catch(err =>{
                console.log(err);
            });
            this.editVible = false;
        }
       });
    },
    //更新图片  
    uploadPic(id){
      return this.$store.state.HOST+'/songList/updateSongListPic?id='+id
    },

    //删除一名歌单
    deleteRow(){
        deleteSongList(this.idx)
        .then(res=>{
            if(res){
              this.getData();
              this.notify("删除成功","success");
            }else{
              this.notify("删除失败","error");
            }
        })
        .catch(err =>{
            console.log(err);
        });
        this.deleteVisible = false;
    },

    //转向歌曲管理页面
    songEdit(id,name){
      this.$router.push({path:'/ListSong',query:{id}});
    },
    //转向歌单的评论列表
    getComment(id){
      this.$router.push({path:'/comment',query:{id}});
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.songList-img{
  width: 100%;
  height: 80px;
  border-radius: 5px;
  margin-bottom: 5px;
  overflow: hidden;
}

.handle-input{
  width: 300px;
  display: inline-block;
}
.pagination{
  display: flex;
  justify-content: center;
}
</style>


